import tuiModal from "@/components/modal/modal"
export default {
	components: {
		tuiModal
	},
	data() {
		return {
			secret: {},
			pageid: 1,
			status: '',
			headerPosition: "fixed",
			headerTop: "0px",
			orderType: [{
				name: '全部',
				sort: 's',
			}, {
				name: '未付款',
				sort: '0'
			}, {
				name: '已付款',
				sort: '1'
			}, {
				name: '配货中',
				sort: '2'
			}, {
				name: '已发货',
				sort: '3'
			}, {
				name: '已收货',
				sort: '4'
			}, {
				name: '已作废',
				sort: '5'
			}, {
				name: '已删除',
				sort: '6'
			}, {
				name: '已退款',
				sort: '8'
			}],
			//订单列表 演示数据
			orderList: [],
			list: [],
			tabbarIndex: 0,
			scrollLeft: 0, //tab标题的滚动条位置
			txt: '', //退款原因
			// 退款弹框
			txtmodal: false,
			refundList:[],
			loadingText:'正在加载中...'
		};
	},
	onLoad(option) {
		this.tabbarIndex = option.tbIndex;
		uni.getStorage({
			key: 'user',
			success: (res) => {
				this.secret = res.data;
				this.getlist();
			}
		})

		//兼容H5下排序栏位置









	},
	onShow() {

	},
	onPageScroll(e) {
		return;
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop >= 0 ? "fixed" : "absolute";
	},
	methods: {
		// 获取订单
		getlist() {
			let data = {
				xopenid: this.secret.openid,
				pageid: this.pageid,
				status: this.tabbarIndex != "s" ? this.tabbarIndex : '',
				secret: this.secret.secret,
				timestamp: this.secret.timestamp
			};

			this.$xm.post('/Order/getList', data, res => {

				if (res.orderlist.length > 0) {
					res.orderlist.forEach(ele => {
						this.$xm.requestImg(ele.pro);

						switch (ele.order_state) {
							case '0':
								ele.tips = '未付款'
								break;
							case '1':
								ele.tips = '已付款'
								break;
							case '2':
								ele.tips = '配货中'
								break;
							case '3':
								ele.tips = '已发货'
								break;
							case '4':
								ele.tips = '已收货'
								break;
							case '5':
								ele.tips = '已作废'
								break;
							case '6':
								ele.tips = '已删除'
								break;
							case '7':
								ele.tips = '已退款'
								break;
							case '8':
								ele.tips = '已退款'
								break;
							default:

						}
					})
				}
				this.list = res.orderlist;
				this.loadingText='到底了'
			})
		},
		// 切换导航 
		showType(tbIndex) {
			if (!tbIndex) {
				tbIndex = "s"
			}
			this.tabbarIndex = tbIndex;
			this.getlist();
		},
		// 跳转详情页
		toOrderDtl(e) {
			console.log(e);
			uni.navigateTo({
				url: './orderDtl/orderDtl?id=' + e
			})
		},
		showLogistics(row) {

		},
		//再次购买
		buyAgain(row) {
			let data = {
				xopenid: this.secret.openid,
				ordersn: row.order_sn,
				secret: this.secret.secret,
				timestamp: this.secret.timestamp
			};

			this.$xm.post('/Order/buyAgain', data, res => {
				if (res.s == 1) {
					uni.navigateTo({
						url: '../../order/confirmation'
					})
				}
			})
		},
		// 修改地址
		edit(row) {
			uni.navigateTo({
				url: './orderEdit/orderEdit?sn=' + row.order_sn
			})
		},
		modalShow(row){
			this.txtmodal=true;
			this.refundList=row;
		},
		// 退款
		refund() {
			console.log(this.refundList);
			this.txtmodal = false;
			let that = this;
			uni.showModal({
				title: '确定要申请退款吗',
				content: '申请后不可恢复',
				success: (res) => {
					if (res.confirm) {
						let params = {
							status: 7,
							xopenid: this.secret.openid,
							ordersn: this.refundList.order_sn,
							secret: this.secret.secret,
							timestamp: this.secret.timestamp,
							memo: this.txt
						}
						this.$xm.post('/Order/changeStatus', params, eve => {
							if (eve.s == 1) {
								uni.showToast({
									title: '申请成功'
								})
								setTimeout(function() {
									that.getlist()
								}, 500)
							} else {
								uni.showToast({
									title: '申请失败'
								})
							}
						})
					} else if (res.cancel) {
						uni.showToast({
							title: '申请失败'
						})
					}
				}
			});


		},
		del(row) {
			let data = {
				xopenid: this.secret.openid,
				ordersn: row.order_sn,
				secret: this.secret.secret,
				timestamp: this.secret.timestamp,
				status: '6'
			}
			this.$xm.post('/Order/changeStatus', data, (res) => {
				console.log(res);
				if (res.s == 1) {
					this.getlist();
					uni.showToast({
						title: '删除成功'
					})
				} else {
					uni.showToast({
						title: '删除失败'
					})
				}
			})
		},
		remindDeliver(row) {
			uni.showToast({
				title: '已提醒商家发货'
			})
		},
		cancelOrder(row) {
			uni.showModal({
				title: '取消订单',
				content: '确定取消此订单？',
				success: (res) => {
					if (res.confirm) {
						console.log('用户点击确定');
						this.doCancelOrder(row.ordersn);
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		doCancelOrder(ordersn) {
			let typeNum = this.orderList.length;
			for (let i = 0; i < typeNum; i++) {
				let list = this.orderList[i];
				let orderNum = list.length;
				if (orderNum > 0 && list[0].type == 'unpaid') {
					for (let j = 0; j < orderNum; j++) {
						if (this.orderList[i][j].ordersn == ordersn) {
							this.orderList[i][j].type = 'cancelled';
							break;
						}
					}
				}

			}
		},
		// 付款
		toPayment(row) {

			let params = {
				xopenid: this.secret.openid,
				order_sn: row.order_sn,
				secret: this.secret.secret,
				timestamp: this.secret.timestamp
			}
			this.$xm.post('/Order/rePay', params, res => {
				if (res.s == 1) {
					let data = res.data
					uni.requestPayment({
						timeStamp: data.timeStamp,
						nonceStr: data.nonceStr,
						package: data.package,
						signType: data.signType,
						paySign: data.paySign,
						success: function(res) {
							console.log(res)
						},
						fail: function(res) {
							console.log(res)
						},
						complete: function(res) {
							if (res.errMsg == 'requestPayment:ok') {
								uni.showModal({
									title: '支付成功！',
									content: '',
									showCancel: false,
									confirmText: '确  定',
									confirmColor: '#2c2c2c',
									success: function(res) {
										that.getlist()
									}
								})
							} else {
								uni.showModal({
									title: '支付失败！',
									content: '',
									showCancel: false,
									confirmText: '确  定',
									confirmColor: '#2c2c2c',
									success: function(res) {
										if (res.confirm) {

										}
									}
								})
							}

						}
					});
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			})

		},
		toPage() {
			uni.navigateTo({
				url: './orderDtl/orderDtl'
			})
		}
	}
}
